import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  brandfamilyAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Brandfamily/brandfamilySlice";
import { getMiniBrands } from "../../../../store/mini/miniSlice";

const AddBrandfamily = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniBrands();
  }, []);
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.rowdata?.name,
      brand_id: props.rowdata.brand_id,
    };
    if (obj.name != null || obj.name == "") {
      props.brandfamilyAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.rowdata?.id,
      name: props.rowdata?.name,
      brand_id: props.rowdata.brand_id,
    };
    props.brandfamilyAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.rowdata.id == null || props.rowdata.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.rowdata.id == null || props.rowdata.id == 0 ? "Add" : "Update"}{" "}
      Brand Family
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.rowdata.name,
      brand_id: props.rowdata.brand_id
    })
  }, [props.rowdata]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.rowdata.id == null) {
            handleAdd();
          }
          else {
            handleUpdate();
          }
        }}>
        <Form.Item label="Brands :"
          name="brand_id"
          rules={[
            {
              required: true,
              message: "Please select Your Brand Name!",
            },
          ]}
          hasFeedback>
          <Select
            showSearch
            placeholder="Select Brand"
            filterOption={(input, option) =>
              option.children.toLowerCase().includes(input.toLowerCase())
            }
            optionFilterProp="brand"
            onChange={(value) => {
              props.InputChangeValue({
                key: "brand_id",
                value: value,
              });
            }}
            value={
              props.rowdata.brand_id == null ? "" : props.rowdata?.brand_id
            }
          >
            {props.miniBrandsList.map((brand, index) => {
              return (
                <Option key={index} value={brand.id}>
                  {brand.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>


        <Form.Item label="Brand Family Name :"
          name="name"
          rules={[
            {
              required: true,
              message: "Please input Your Brand Family Name!",
            },
          ]}
          hasFeedback>
          <Input
            id="name"
            name="name"
            style={{ width: "100%" }}
            placeholder="Brand Family Name"
            value={props.rowdata?.name}
            onChange={ChangeHandeler}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ brandfamily, mini }) {
  return {
    rowdata: brandfamily.rowdata,
    model: brandfamily.model,
    miniBrandsList: mini.miniBrandsList,
  };
}

export default connect(mapStateToProps, {
  brandfamilyAdd,
  isModelVisible,
  InputChangeValue,
  getMiniBrands,
})(AddBrandfamily);
