import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";

import {
  getBrandfamilys,
  brandfamilyEdit,
  brandfamilyDelete,
  isModelVisible,
  SetBrandfamilysParams,
} from "../../../../store/Masters/Brandfamily/brandfamilySlice";

import { getMiniBrands } from "../../../../store/mini/miniSlice";

import AddBrandfamily from "./addBrandfamily";

function Brandfamily(props) {
  useEffect(() => {
    props.getBrandfamilys({ page: 1, search: "", page_size: 10 });
  }, []);
  const { Search } = Input;
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Brand Family Name",
      render: (row) => <span className={columnClass}>{row.name}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: "40%",
    },
    {
      title: "Brand Name",
      filterMode: "tree",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "20%",
      render: (row) => <span className={columnClass}>{row.brand?.name}</span>,
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_brandfamily") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.brandfamilyEdit(row);
              props.getMiniBrands({});
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_brandfamily") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.brandfamilyDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.brandfamilylist.map((row, key) => ({
    ...row,
    index:
      (props.brandfamilyParams.page - 1) * props.brandfamilyParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetBrandfamilysParams({
      ...props.brandfamilyParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getBrandfamilys({
      ...props.brandfamilyParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetBrandfamilysParams({ ...props.brandfamilyParams, search: val });
    props.getBrandfamilys({ ...props.brandfamilyParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }

  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Brand Family Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_brandfamily") !== -1 &&
          "Add Brand Family"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-16">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getBrandfamilys({
                      ...props.brandfamilyParams,
                      page: 1,
                      search: "",
                    });
                    props.SetBrandfamilysParams({
                      ...props.brandfamilyParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.brandfamilyParams?.page}
                pageSize={props.brandfamilyParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddBrandfamily />
    </Row>
  );
}

function mapStateToProps({ brandfamily, system }) {
  return {
    brandfamilylist: brandfamily.brandfamilylist,
    noofpages: brandfamily.noofpages,
    listCount: brandfamily.listCount,
    brandfamilyParams: brandfamily.brandfamilyParams,
    loading: brandfamily.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getBrandfamilys,
  brandfamilyEdit,
  brandfamilyDelete,
  isModelVisible,
  SetBrandfamilysParams,
  getMiniBrands,
})(Brandfamily);
